@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap');
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    text-align: center;
    background-color: #f8f9fa;
}
header {
    font-family: "Nanum Pen Script", cursive;
    font-style: italic;
    background-color: #333;
    color: white;
    padding: 20px 0;
    font-size: 24px;
    font-weight: bold;
}
.nav {
    display: flex;
    justify-content: center;
    background-color: #444;
    padding: 10px 0;
}
.nav a {
    color: white;
    text-decoration: none;
    padding: 10px 20px;
    margin: 0 10px;
    font-size: 18px;
}
.nav a:hover {
    background-color: #555;
    border-radius: 5px;
}
.container {
    max-width: 1000px;
    margin: auto;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}
.text {
    text-decoration: none;
    color: black;
    background-color: #ffeb00;
    padding: 2px 4px;
}
.contact-links a {
    display: block;
    margin: 10px 0;
    padding: 10px;
    font-size: 18px;
    text-decoration: none;
    border-radius: 5px;
    color: white;
}
.instagram { background-color: #E1306C; }
.email { background-color: #007bff; }
.kakao { background-color: #ffeb00; color: #3c1e1e; }
.gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 10px;
    padding: 20px;
}
.gallery img {
    width: 100%;
    border-radius: 8px;
    transition: 0.3s;
}
.gallery img:hover {
    transform: scale(1.05);
}
.lightbox {
    display: none;
    position: fixed;
    top: 0; left: 0;
    width: 100%; height: 100%;
    background-color: rgba(0,0,0,0.8);
    justify-content: center;
    align-items: center;
}
.lightbox.open {
    display: flex;
}
.lightbox img {
    max-width: 90%;
    max-height: 80%;
    border-radius: 8px;
}
.close {
    position: absolute;
    top: 20px; right: 30px;
    font-size: 40px;
    color: white;
    cursor: pointer;
}
